import React , { useState , useEffect }from 'react';
import Main from '../components/Main'
import Content from '../components/Content'
import Contact from '../components/Contact'


function Home() {
    const [sliders , setSliders] = useState([])
    const [pizzas , setPizzas] = useState([]);
    const [snacks , setSnacks] = useState([]);
    const [desserts ,setDesserts] = useState([]); 
    const [drinks , setDrinks] = useState([]);
    const [combos , setCombos] = useState([]);
    const [load , setLoad] = useState(false)

    const items = ['Пиццы' , 'Комбо' ,'Закуски' , 'Десерты' ,  'Напитки'];
    useEffect(() => {
        fetch('https://github.com/ryskeldi2237/json')
        .then((response) => response.json())
        .then((data) =>{ 
        setSliders(data.sliders);
        setPizzas(data.pizzas);
        setSnacks(data.snacks);
        setDesserts(data.desserts);
        setDrinks(data.drinks);
        setCombos(data.combos);
        setLoad(true);
        
    });
    } , [])
    return (
        <div>
            <Main sliders={sliders}/>
            <Content items={items} pizzas={pizzas} snacks={snacks} desserts={desserts} drinks={drinks} combos={combos} loading={load}/>
            <Contact/>
        </div>
    )
}

export default Home
